.timeline-post {
  $border: 1px solid rgb(240, 240, 240);

  width: 100%;
  border: $border;
  border-radius: 8px;
  background-color: white;

  .post-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: $border;
    padding: 8px 30px;

    .timestamp {
      margin: 0;
    }
  }

  .post-body {
    padding: 30px;
  }

  .post-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 10px;

    .action-btn-group {
      .repost {
        margin-right: 5px;
      }
    }
  }
}
