.artist-tile {
  border: 1px solid rgb(219, 219, 219);
  border-radius: 5px;
  width: 100%;

  a {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 15px;

    .avatar {
      margin-right: 20px;
    }

    .body {
      flex: 1;
      .username {
        font-size: 1.1em;
        margin-bottom: 5px;
      }
    }
  }
}
